export * from './outline';
export * from './edge';
/*
 * è®¡ç®—åž‚ç›´è¾¹çš„ä¸Žèµ·å§‹ç‚¹æœ‰ä¸€å®šè·ç¦»å¯¹ç§°ï¼Œè¾¹åž‚ç›´äºŽè¾¹çš„ç‚¹
 * è°ƒç”¨æ–¹ï¼šè®¡ç®—ç®­å¤´ä½ç½®ï¼Œè®¡ç®—æ‰©å¤§å˜å¾—ç‚¹å‡»åŒºåŸŸ
 */
export var getVerticalPointOfLine = function (config) {
    /*
     ** offset: ç«¯ç‚¹åˆ°åž‚ç›´ç‚¹çš„è·ç¦»
     ** verticalLength: ä¸¤è¾¹ç‚¹åˆ°åž‚ç›´ç‚¹çš„è·ç¦»
     ** type: æ ‡è¯†æ±‚çš„æ˜¯çº¿æ®µçš„å¼€å§‹ç«¯ç‚¹/ç»“æŸç«¯ç‚¹
     */
    var start = config.start, end = config.end, offset = config.offset, verticalLength = config.verticalLength, type = config.type;
    var pointPosition = {
        leftX: 0,
        leftY: 0,
        rightX: 0,
        rightY: 0,
    };
    // // è¾¹ä¸Žæ°´å¹³çº¿çš„å¤¹è§’
    var angleOfHorizontal = Math.atan((end.y - start.y) / (end.x - start.x));
    // è¾¹å’Œä¸¤è¾¹ç‚¹çš„å¤¹è§’
    var angleOfPoints = Math.atan(offset / verticalLength);
    // ç‚¹åˆ°èµ·ç‚¹çš„è·ç¦»
    var len = Math.sqrt(verticalLength * verticalLength + offset * offset);
    if (type === 'start') {
        if (end.x >= start.x) {
            pointPosition.leftX =
                start.x + len * Math.sin(angleOfHorizontal + angleOfPoints);
            pointPosition.leftY =
                start.y - len * Math.cos(angleOfHorizontal + angleOfPoints);
            pointPosition.rightX =
                start.x - len * Math.sin(angleOfHorizontal - angleOfPoints);
            pointPosition.rightY =
                start.y + len * Math.cos(angleOfHorizontal - angleOfPoints);
        }
        else {
            pointPosition.leftX =
                start.x - len * Math.sin(angleOfHorizontal + angleOfPoints);
            pointPosition.leftY =
                start.y + len * Math.cos(angleOfHorizontal + angleOfPoints);
            pointPosition.rightX =
                start.x + len * Math.sin(angleOfHorizontal - angleOfPoints);
            pointPosition.rightY =
                start.y - len * Math.cos(angleOfHorizontal - angleOfPoints);
        }
    }
    else if (type === 'end') {
        if (end.x >= start.x) {
            pointPosition.leftX =
                end.x + len * Math.sin(angleOfHorizontal - angleOfPoints);
            pointPosition.leftY =
                end.y - len * Math.cos(angleOfHorizontal - angleOfPoints);
            pointPosition.rightX =
                end.x - len * Math.sin(angleOfHorizontal + angleOfPoints);
            pointPosition.rightY =
                end.y + len * Math.cos(angleOfHorizontal + angleOfPoints);
        }
        else {
            pointPosition.leftX =
                end.x - len * Math.sin(angleOfHorizontal - angleOfPoints);
            pointPosition.leftY =
                end.y + len * Math.cos(angleOfHorizontal - angleOfPoints);
            pointPosition.rightX =
                end.x + len * Math.sin(angleOfHorizontal + angleOfPoints);
            pointPosition.rightY =
                end.y - len * Math.cos(angleOfHorizontal + angleOfPoints);
        }
    }
    return pointPosition;
};
